.btn_btn__omS_V {
    position: relative;
    text-transform: uppercase;
    text-decoration: none;
    display: inline-block;
    letter-spacing: 1px;
    overflow: hidden;
    width: 150px;
    height: 50px;
    margin: 20px;
    font-size: 16px;
    background-color: transparent;
    border: 1px solid blue;
    border-radius: 5px;
    cursor: pointer;
    font-weight: 500;
    color: black;
    transition: box-shadow 0.5s ease-in-out, background-color 0.3s ease-in-out, color 0.5s ease-in-out,
    opacity 0.3s ease-in-out, border 0.3s ease-in-out;
}

.btn_line__1ulnf {
    position: absolute;
    display: block;
}

.btn_lineTop__3pisj {
    top: 0;
    left: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(90deg, transparent, blue);
    transition: left 0.5s ease-in-out;
}

.btn_btn__omS_V:hover .btn_lineTop__3pisj {
    left: 100%;
    transition: left 0.5s ease-in-out;
    transition-delay: 0.25s;
}

.btn_lineBottom__3dEE5 {
    bottom: 0;
    right: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(270deg, transparent, blue);
    transition: right 0.5s ease-in-out;
}

.btn_btn__omS_V:hover .btn_lineBottom__3dEE5 {
    right: 100%;
    transition: right 0.5s ease-in-out;
    transition-delay: 0.25s;
}

.btn_lineLeft__2aQ00 {
    bottom: -100%;
    left: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(0deg, transparent, blue);
    transition: bottom 0.5s ease-in-out;
}

.btn_btn__omS_V:hover .btn_lineLeft__2aQ00 {
    bottom: 100%;
    transition: bottom 0.5s ease-in-out;
    transition-delay: 0s;
}

.btn_lineRight__1XqCM {
    top: -100%;
    right: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(0deg, transparent, blue);
    transition: top 0.5s ease-in-out;
}

.btn_btn__omS_V:hover .btn_lineRight__1XqCM {
    top: 100%;
    transition: top 0.5s ease-in-out;
    transition-delay: 0s;
}

.btn_btn__omS_V:hover {
    box-shadow: 0 0 10px blue, 0 0 30px blue;
    background-color: blue;
    transition: box-shadow 0.5s ease-in-out, background-color 0.3s ease-in-out, color 0.5s ease-in-out;
    transition-delay: 0.5s;
    color: white;
}

